// Visualizador psilon-Delta
const EpsilonDeltaVisualizer = () => {
  const [epsilon, setEpsilon] = useState(1.0);
  const a = 2;
  const L = 4; // f(x) = x^2 en x=2  4
  const delta = Math.sqrt(L + epsilon) - a; // Aproximacin para f(x)=x^2

  const data = {
    labels: Array.from({ length: 100 }, (_, i) => ((i * 3.0) / 100 + 0.5).toFixed(2)),
    datasets: [
      {
        label: 'f(x) = x',
        data: Array.from({ length: 100 }, (_, i) => {
          const x = (i * 3.0) / 100 + 0.5;
          return x * x;
        }),
        borderColor: '#1F2937',
        backgroundColor: 'rgba(31, 41, 55, 0.1)',
        fill: false,
      },
      {
        label: 'Banda  (superior)',
        data: Array.from({ length: 100 }, () => L + epsilon),
        borderColor: '#FDBA74',
        borderDash: [5, 5],
        pointRadius: 0,
      },
      {
        label: 'Banda  (inferior)',
        data: Array.from({ length: 100 }, () => L - epsilon),
        borderColor: '#FDBA74',
        borderDash: [5, 5],
        pointRadius: 0,
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: { display: false },
      tooltip: { mode: 'index', intersect: false },
    },
    scales: {
      x: {
        type: 'linear',
        title: { display: true, text: 'x' },
        min: 0.5,
        max: 3.5,
        grid: { color: '#E5E7EB' },
        ticks: { color: '#6B7280' },
      },
      y: {
        title: { display: true, text: 'f(x)' },
        min: 0,
        max: 12,
        grid: { color: '#E5E7EB' },
        ticks: { color: '#6B7280' },
      },
    },
    animation: false,
  };

  return (
    <div className="mt-4 mb-5">
      <h4>Visualizador Interactivo de la Definicin -</h4>

      <div style={{ height: '300px', marginBottom: '20px' }}>
        <Line 
          data={data} 
          options={options} 
          key={`epsilon-delta-chart-${epsilon}`}
        />
      </div>

      <div className="mb-3">
        <label>
          <strong> (tolerancia vertical):</strong> {epsilon.toFixed(2)}
        </label>
        <input
          type="range"
          min="0.1"
          max="2"
          step="0.01"
          value={epsilon}
          onChange={(e) => setEpsilon(parseFloat(e.target.value))}
          style={{ width: '100%', marginTop: '10px' }}
        />
      </div>

      <p><strong> calculado:</strong> {Math.abs(delta).toFixed(2)}</p>

      <p style={{ fontSize: '0.95rem', color: '#4B5563' }}>
        Juega con el slider! Elige cualquier valor para  (la tolerancia vertical en naranja ). La aplicacin encontrar un  (la proximidad horizontal en azul ) que garantiza que la funcin se mantiene dentro de esa tolerancia. Esto demuestra que el lmite existe!
      </p>
    </div>
  );
};
